.card-container-head{width:100%;margin:30px 0px;display:flex;flex-direction:column;justify-content:center;align-items:center;align-content:center}.card-container{width:100%;padding:10px;display:grid;grid-column-gap:100px;-webkit-column-gap:100px;column-gap:100px;justify-content:space-around;grid-template-columns:repeat(auto-fit, minmax(400px, 2fr));max-width:1400px}.card-container .card-container__left,.card-container .card-container__right{min-width:300px;flex:1 1 50%;display:flex;justify-content:flex-start;align-items:flex-start;align-content:flex-start;flex-direction:column}.card-container .card-container__left .card-title{display:grid;background:#000;border:1px solid gray;grid-template-columns:7fr 1fr;width:100%;min-height:50px}.card-container .card-container__left .card-title .card-title__flag{min-width:100px;width:100%;height:100%}.card-container .card-container__left .card-title h1{color:#fff;font-family:"Oswald",sans-serif;padding:15px}.card-container .card-container__left .card-body{background:#f7f7f7;text-transform:capitalize;display:flex;flex-direction:column;justify-content:center;align-items:center;justify-items:center;align-content:center;width:100%}.card-container .card-container__left .card-body .card-body__item{font-size:1.2rem;border:1px solid gray;border-top:none;width:100%;padding:5px 15px;display:grid;grid-template-columns:1.5fr 1.6fr .3fr}.card-container .card-container__left .card-body .card-body__item img{-webkit-transform:scale(0.5) skew(-15deg);transform:scale(0.5) skew(-15deg)}.card-container .card-container__left .card-body .card-body__item div{width:100%;margin:auto 0}.card-container .card-container__left .card-body .card-body__item .field{font-family:"Oswald",sans-serif}.card-container .card-container__right{background:#131216;padding:10px;min-height:400px}.card-container .card-container__right .card-circle-container{padding:10px 0px;display:grid;grid-template-columns:1fr 1fr;width:100%;min-height:100%}.card-container .card-container__right .card-circle-container .card-circle-item{color:#f8f8f8;display:flex;flex-direction:column;justify-content:center;align-items:center;align-content:center}.card-container .card-container__right .card-circle-container .card-circle-item .circle{width:100%;height:100%;display:flex;flex-direction:column;justify-content:center;max-height:160px;min-height:70px;max-width:160px;min-width:70px;font-size:1.3rem;font-family:"Oswald",sans-serif;font-weight:800;border:2px solid #ce3a4d;border-radius:50%;text-align:center}.card-container .card-container__right .card-circle-container .card-circle-item h2{text-transform:capitalize;margin-bottom:7px;font-size:2rem;color:#f8f8f8;font-family:"Oswald",sans-serif;text-align:center}.card-container .card-container__footer .card-source{margin-top:10px;font-size:1.2rem}.card-container .card-container__footer .card-source small{font-size:1rem}@media screen and (max-width: 700px){.card-container{grid-template-columns:repeat(auto-fit, minmax(200px, 2fr))}.card-container .card-container__right .card-circle-container .card-circle-item h2{font-size:1.5rem}}
.on{transition:all 200ms ease-in-out;-webkit-transform:translateX(0%);transform:translateX(0%)}.show{opacity:0;display:none;position:fixed;top:15px;left:15px;background:#ce3a4d;width:25px;height:25px;border-radius:50%;z-index:10000000;transition:all 200ms ease-in-out;box-shadow:0px 0px 5px 1px rgba(20,20,20,.5)}.open{-webkit-transform:scale(1.15) translateY(15px);transform:scale(1.15) translateY(15px);border:1px solid #f8f8f8;background:#366db6}nav{transition:all 200ms ease-in-out;z-index:9;font-family:"Oswald",sans-serif;color:#f8f8f8;font-style:italic;font-size:1.2rem;min-width:225px}nav .nav__holder{width:100%;position:fixed;width:225px;left:0}nav h1{background-color:#131216;color:#f8f8f8;padding-bottom:20px;width:250;font-size:2rem;letter-spacing:.2rem;padding-top:20px;font-weight:300;text-align:center}nav .nav__navigation{width:225;padding:20px 0px;padding-bottom:50px;list-style:none;display:flex;flex-direction:column;justify-content:space-around}nav .nav__navigation li{padding-left:15px;width:200;letter-spacing:.08rem;display:flex;flex-direction:row;align-items:center;align-content:center;transition:200ms all ease-in}nav .nav__navigation li:hover{cursor:pointer;background:#141414}nav .nav__navigation li:hover a{color:#366db6;-webkit-transform:translateX(5px);transform:translateX(5px)}nav .nav__navigation li:hover img{-webkit-filter:invert(1);filter:invert(1)}nav .nav__navigation li a{transition:200ms all ease-in;padding-left:10px;line-height:4rem;width:100%}nav .nav__navigation li img{transition:200ms all ease-in;-webkit-transform:scale(0.9);transform:scale(0.9);display:inline;width:40px;height:40px}@media screen and (max-width: 700px){nav{-webkit-transform:translateX(-100%);transform:translateX(-100%);position:fixed;left:0;top:0;height:100%;background:#ce3a4d;z-index:1100}nav *{z-index:1100}.show{display:block;opacity:1}}
